.back-to-top {

  position: fixed;
  bottom: 40px;
  right: 1px;
  z-index: 1;
  width: 50px;
  height: 50px;
  background: $AMSDarkGray;
  opacity: 0.75;
  display: none;

  &:hover {
    opacity: 1;
    cursor: pointer;
  }

  i {
    font-size: 25px;
    padding: 12px;
    color: #FFF;
  }

}