.mars-accordion-section {
  border: 1px solid $AMSGrassGreen;
  margin-bottom: 20px;

  .mars-accordion-content {
    padding: 15px 15px 15px 15px;

    &.collapse {
      display: none!important;
      &.in { display: block!important; }
    }

    .col-sm-4, .col-sm-2 {
      padding-right: 50px;

      p {
        margin: 15px 0 5px 0;
      }
    }

    .action-bar {
      clear: both;
      margin-top: 20px;
    }
  }

  .mars-accordion-header {
    background-color: $AMSGrassGreen;
    cursor: pointer;
    margin: -1px 0;
    color: #fff !important;

    h3 {
      text-transform: capitalize !important;
      font-size: 16px;
      height: 44px;
      margin: 0 !important;
      line-height: 44px;

      &.popover-title {
        margin: 0 !important;
      }

      i.fa {
        margin-right: 5px;
      }
    }
  }
}

.mars-form-control {
  &.btn {
    background-color: $AMSGrassGreen;
    color:#fff;
  }
}

.custom-datepicker{
  input.form-control {
    border: none;
    box-shadow: none;
    background: none;
    display: inline-block;
  }

  &:focus {
    border: 1px solid $AMSFocusBlue !important;
    box-shadow: 0 0 5px $AMSFocusBlue !important;
  }
  &.disabled{
    opacity: 0.5 !important;
    cursor: default;
    &:hover{
      border: 1px solid #ccc!important;
    }
  }
}

.uib-datepicker-popup {
  background-color: $AMSGrassGreen;
  padding: 10px;
  z-index: 9999;

  &.dropdown-menu:after {
    border-bottom: 6px solid $AMSGrassGreen;
  }

  .btn-info, .btn-info.active, .open>.btn-info.dropdown-toggle {
    background-color: $AMSSpringGreen;
    border-color: $AMSSpringGreen;
    color: $AMSDarkGreen !important;
    .text-info {
      color: $AMSDarkGreen !important;
    }
  }

  .btn-sm, .btn-group-sm>.btn {
    padding: 6px 10px;
    font-size: 13px;
  }

  .uib-daypicker {
    background-color: #fff;
    padding: 5px;
    border-radius: 5px;

    th {
      font-size: 15px;
    }

  }

  .uib-button-bar {
    .uib-datepicker-current {
      padding: 6px 2px !important;
      width: 55px;
      margin-right: 8px;
      font-weight: normal;
    }
    .btn-danger {
      background-color: #ccc;
      border-color: #ccc;
      color: $AMSDarkGreen !important;
    }
    .btn-success {
      background-color: $AMSSpringGreen;
      color: $AMSDarkGreen !important;
    }
  }
}

#section-result {
  .dataTable {
    th, td {
      font-size: 14px !important;
      text-align: center;
    }

    th {
      background: $AMSLightGray;
    }
  }

  .no-data {
    padding: 0;
    margin: 0;
    text-align: center;
    font-size: 16px;
  }

  .group-key {
    margin: 0;
    padding: 8px 10px;
    background: #ccc;
    font-size: 14px;
    text-transform: uppercase;
    font-weight: bold;
  }

  .dataTables_length {
    margin-left: 5px;
  }

  ul.pagination {
    margin-top: 0;
  }

  .control-bar {
    margin-left: -5px;

    a.btn {
      padding: 5px 10px;
    }
  }

  .group-records {
    margin-top: 10px;
  }
}

.angucomplete-holder {
  position: relative;
}

.angucomplete-dropdown {
  border-color: #ececec;
  border-width: 1px;
  border-style: solid;
  border-radius: 2px;
  width: 250px;
  padding: 6px;
  cursor: pointer;
  z-index: 9999;
  position: absolute;
  /*top: 32px;
  left: 0px;
  */
  margin-top: -6px;
  background-color: #ffffff;
  text-align: left;
}

.angucomplete-searching {
  color: #acacac;
  font-size: 14px;
}

.angucomplete-description {
  font-size: 12px;
  color: #CCCCCC;
}

.angucomplete-row {
  padding: 5px;
  color: #000000;
  margin-bottom: 4px;
  clear: both;
}

.angucomplete-selected-row {
  background-color: #EAEAEA;
}

.angucomplete-image-holder {
  padding-top: 2px;
  float: left;
  margin-right: 10px;
  margin-left: 5px;
}

.angucomplete-image {
  height: 34px;
  width: 34px;
  border-radius: 50%;
  border-color: #ececec;
  border-style: solid;
  border-width: 1px;
}

.angucomplete-image-default {
  /* Add your own default image here
   background-image: url('/assets/default.png');
  */
  background-position: center;
  background-size: contain;
  height: 34px;
  width: 34px;
}
